<template>
	<div class="panel">
		<h2>买家年龄分布</h2>
		<div ref="pieChart" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'PiePanel',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.pieChart);
				if(!chart){
					chart = this.$echarts.init(this.$refs.pieChart);
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					color: ["#06d2ab", "#06beab", "#06aaab", "#0696ab", "#0682ab", "#066eab", "#065aab", "#0646ab"],
					tooltip: {
						trigger: 'item',
						formatter: "<b>{a}</b> <br/> {b} : {c} <i>({d}%)</i>",
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					series: [{
						name: '年龄',
						type: 'pie',
						center: ["50%", "50%"],
						radius: ['55%', '85%'],
						label: {
							show: false,
							position: 'center'
						},
						labelLine: {
							show: false
						},
						avoidLabelOverlap: false,
						emphasis: {
							label: {
								show: true,
								fontSize: '26',
								fontWeight: 'bold'
							}
						},
						data: [{
								value: this.list[0],
								name: '0~18'
							},
							{
								value: this.list[1],
								name: '18~24'
							},
							{
								value: this.list[2],
								name: '25~29'
							},
							{
								value: this.list[3],
								name: '30~34'
							},
							{
								value: this.list[4],
								name: '35~39'
							},
							{
								value: this.list[5],
								name: '40~49'
							},
							{
								value: this.list[6],
								name: '50~∞'
							},
							{
								value: this.list[7],
								name: 'unknow'
							},
						]
					}]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/age.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list.push(resp.data[i][1]);
				}
				this.drawChart();
			})
		}
	}
</script>

